___CSS_LOADER_EXPORT___.push([module.id, "\n.block[data-v-58c4e442] {\n    background-color: white;\n    opacity: 0.8;\n    height: 246px;\n    width: 332px;\n    position: absolute;\n    z-index: 1;\n}\n.dotMenu[data-v-58c4e442]{\n    cursor: pointer;\n}\n.nonLogo[data-v-58c4e442]{\n    font-size: 36pt;\n}\n.modulesInfoDiv[data-v-58c4e442]{\n    margin-top: 20px;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/application/appCard.vue"],"names":[],"mappings":";AAqFA;IACI,uBAAuB;IACvB,YAAY;IACZ,aAAa;IACb,YAAY;IACZ,kBAAkB;IAClB,UAAU;AACd;AACA;IACI,eAAe;AACnB;AAEA;IACI,eAAe;AACnB;AACA;IACI,gBAAgB;AACpB","sourcesContent":["<script setup lang=\"ts\">\nimport { defineAsyncComponent, ref } from 'vue'\nimport { container } from '../../../../domain/core/diContainer';\nimport { IApplication } from \"../../../../domain/model/models\"\nimport { IRouter } from '../../../../domain/presentation/IRouter';\nimport { IObjectUseCase } from '../../../../domain/useCase/IObjectUseCase';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport 'bootstrap/js/dist/dropdown'\nimport { IDialog } from '../../../../domain/presentation/IDialog';\nimport { computed } from '@vue/reactivity';\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst objectUseCase = container.resolve<IObjectUseCase>(IObjectUseCase);\nconst router = container.resolve<IRouter>(IRouter);\nconst dialog = container.resolve<IDialog>(IDialog);\n\nconst props = defineProps<{ app: IApplication }>();\nconst appColor = `border-left:8px solid ${props.app.color} !important;`\nconst block = computed(() => !props.app.synced);\n\nfunction openApp() {\n    router.push(`/studio/${props.app.ID}`);\n}\nfunction updateApp() {\n    dialog.showDialog(defineAsyncComponent(() => import(`../../components/application/addOrUpdateApp.vue`)), { closable: true, title: \"Update Application\" }, {app: props.app});\n}\nfunction deleteApp() {\n    executor.execute(async () => {\n        await objectUseCase.deleteObject(props.app.ID);\n    }, { loading: true });\n}\n\n</script>\n<template>\n    <div class=\"block\" v-if=\"block\"></div>\n    <div class=\"card each-card\" :style=\"appColor\">\n        <div class=\"card-body\">\n            <div class=\"row\">\n                <div class=\"col-3 appLogoDiv \">\n                    <!-- <img  src='../../../../images/is-bankasi-logo.jpg' class=\"card-logo\"> -->\n                     <div v-if=\"!app.logo\" class='mdi mdi-image-outline card-logo nonLogo'></div>\n                    <img v-if=\"app.logo\" :src='app.logo' class=\"card-logo\">\n                </div>\n                <div class=\"col-7\">\n                    <p class=\"card-title\">{{ app.name }}</p>\n                    <p class=\"card-description\">{{ app.description }}</p>\n                    <!-- <p class=\"card-info\">{{app.createdBy}}  {{app.createDate}}</p> -->\n\n                </div>\n                <div class=\"col-2 p-0 dot-icon\">\n                    <i class=\"mdi mdi-dots-vertical vertical-dot-icon dotMenu\" id=\"dropdownMenuButton1\"\n                        data-bs-toggle=\"dropdown\" aria-expanded=\"false\" data-bs-target=\"#dropdown-menu\"></i>\n                    <ul class=\"dropdown-menu\" aria-labelledby=\"dropdownMenuButton1\">\n                        <li><a class=\"dropdown-item\" @click='updateApp()'><i\n                                    class=\"mdi mdi-pencil-outline\"></i>Edit</a></li>\n                        <li><a class=\"dropdown-item list-group-item-danger\" @click=\"deleteApp()\"><i\n                                    class=\"mdi mdi-delete-outline\"></i>Delete</a></li>\n                    </ul>\n                </div>\n                <div class=\"row modulesInfoDiv\" >\n                    <div class=\"col\">\n                        <p class=\"text-center m-0 card-mpt\">Modules</p>\n                        <p class=\"text-center card-numbers\">{{ app!.details!.moduleCount }}</p>\n                    </div>\n                    <div class=\"col dashes\">\n                        <p class=\"text-center m-0 card-mpt\">Pages</p>\n                        <p class=\"text-center card-numbers\">{{ app!.details!.pageCount }}</p>\n                    </div>\n                    <div class=\"col\">\n                        <p class=\"text-center m-0 card-mpt\">Templates</p>\n                        <p class=\"text-center card-numbers\">{{ app!.details!.pageTemplateCount }}</p>\n                    </div>\n                </div>\n                <div class=\"row\">\n                    <p class=\"open-in-studio openApp-link\" @click=\"openApp()\">OPEN IN STUDIO <i class=\"mdi mdi-arrow-right mdi-18px\"></i> </p>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<style>\n@import url(\"../../../../styles/IDE3.css\");\n@import url(\"https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/js/bootstrap.bundle.min.js\");\n</style>\n<style scoped>\n.block {\n    background-color: white;\n    opacity: 0.8;\n    height: 246px;\n    width: 332px;\n    position: absolute;\n    z-index: 1;\n}\n.dotMenu{\n    cursor: pointer;\n}\n\n.nonLogo{\n    font-size: 36pt;\n}\n.modulesInfoDiv{\n    margin-top: 20px;\n}\n</style>"],"sourceRoot":""}]);
